<template>
  <div class="diary">
    <h3>Дневник</h3>
    <Loader v-show="loading" />
    <div v-show="!loading" class="input-field width col s7">
      <select ref="weekSelect" @change="selectWeek" v-model="selectedWeek">
        <option v-for="(week, index) in weeks" :key="index" :value="index">
          {{ week.start | date }} - {{ week.end | date }}</option
        >
      </select>
      <label>Выбери неделю</label>
    </div>
    <div v-show="!loading" class="row width">
      <button
        @click="selectWeek(-1)"
        class="btn waves-effect waves-light light-blue darken-1 col push-s1 s3"
      >
        <i class="material-icons">navigate_before</i>
      </button>
      <button
        @click="selectWeek(1)"
        class="btn waves-effect waves-light light-blue darken-1 col push-s4 s3"
      >
        <i class="material-icons">navigate_next</i>
      </button>
    </div>
    <div
      v-show="!loading && countdown.work"
      style="margin-top: 15px;"
      @click="cancelCountdown"
      class="progress width"
    >
      <div
        class="determinate"
        :style="{ width: countdown.left_time / 50 + '%' }"
      ></div>
    </div>
    <div v-show="!loading" id="diary">
      <div v-for="day in diary" :key="day.date" class="card">
        <h5 class="center">{{ new Date(day.date) | diaryDate }}</h5>
        <div
          v-for="lesson in day.lessons"
          :key="lesson.classmeetingId"
          class="row"
        >
          <span class="col number s1">{{ lesson.number }}</span>
          <div class="col subject s9">
            <span class="name">{{ lesson.subjectName }}</span>
            <span class="info"
              >{{ lesson.startTime }} - {{ lesson.endTime }}, {{ lesson.room }}
            </span>
            <span class="homework">{{ homework(lesson.assignments) }}</span>
          </div>
          <div class="col marks s2">
            <span v-for="mark in marks(lesson.assignments)" :key="mark">{{
              mark
            }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader";

export default {
  data: () => ({
    weeks: [],
    diary: [],
    loading: true,
    instance: null,
    countdown: {
      work: false,
      left_time: 0,
      old_index: 0,
      interval: null
    },
    selectedWeek: null
  }),
  methods: {
    init(range) {
      const now = Date.now();
      const end = new Date(range.end);
      const start = new Date(range.start);
      while (start < end) {
        const week = {
          start: new Date(start),
          end: null
        };
        do {
          start.setDate(start.getDate() + 1);
        } while (start.getDay() != 0);
        week.end = new Date(start);
        if (now >= week.start && now <= week.end - 0 + 8.64e7) {
          this.selectedWeek = this.weeks.length;
        }

        this.weeks.push(week);
        start.setDate(start.getDate() + 1);
      }
      this.loading = false;
      this.loadDiary(this.weeks[this.selectedWeek]);
      if (this.instance && this.instance.destroy) this.instance.destroy();
      setTimeout(() => {
        // eslint-disable-next-line no-undef
        this.instance = M.FormSelect.init(this.$refs.weekSelect);
      }, 0);
    },
    selectWeek(event) {
      if (typeof event != "number") {
        this.loadDiary(this.weeks[this.selectedWeek]);
        return;
      }

      this.countdown.work = true;
      this.countdown.left_time = 5000;
      clearInterval(this.countdown.interval);
      this.selectedWeek = (event > 0 ? 1 : -1) + this.selectedWeek;
      this.countdown.interval = setInterval(() => {
        this.countdown.left_time -= 300;

        if (this.countdown.left_time <= 0) {
          this.countdown.work = false;
          clearInterval(this.countdown.interval);
          this.loadDiary(this.weeks[this.selectedWeek]);
        }
      }, 300);
      setTimeout(() => this.instance._setValueToInput(), 0);
    },
    loadDiary(range) {
      this.loading = true;
      this.GetDiary(range)
        .then(
          ({ weekDays }) => ((this.loading = false), (this.diary = weekDays))
        )
        .catch(err => {
          this.loading = false;
          this.cancelCountdown();
          if (err.code == 4008) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Сайт лег, попробуй позже" });
          } else if (err.code == 4006) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
          } else {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
          }
          console.error(err);
        });
    },
    ...mapActions(["GetRange", "GetDiary"]),
    marks(val) {
      return val?.filter(v => v.mark)?.map(v => v.mark.mark) || [];
    },
    homework(marks) {
      return marks?.find(m => m.typeId == 3)?.assignmentName || "";
    },
    cancelCountdown() {
      this.countdown.work = false;
      clearInterval(this.countdown.interval);
      this.selectedWeek = this.countdown.old_index;
      setTimeout(() => this.instance._setValueToInput(), 0);
    }
  },
  watch: {
    selectedWeek(_val, oldVal) {
      if (oldVal) this.countdown.old_index = oldVal;
    }
  },
  filters: {
    date(val) {
      function num(val) {
        if (val < 10) val = "0" + val;
        return val;
      }
      return `${num(val.getDate())}.${num(val.getMonth() + 1)}.${num(
        val.getFullYear()
      )}`;
    },
    diaryDate(val) {
      const week = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
      const month = [
        "янв",
        "фев",
        "мар",
        "апр",
        "май",
        "июн",
        "июл",
        "авг",
        "сен",
        "окт",
        "ноя",
        "дек"
      ];
      return (
        `${week[val.getDay()]}, ` +
        `${val.getDate()} ` +
        `${month[val.getMonth()]}. ` +
        `${val.getFullYear()} г.`
      );
    }
  },
  created() {
    this.GetRange()
      .then(range => this.init(range))
      .catch(console.error);
  },
  destroyed() {
    if (this.countdown.work) clearInterval(this.countdown.interval);
    if (this.instance && this.instance.destroy) {
      this.instance.destroy();
    }
  },
  components: {
    Loader
  }
};
</script>
